<template>
    <div id="character-list">
        <div class="container">
            <div class="inner">
                <p class="h3 title">Character List</p>

                <div class="list" ref="table">
                    <ul class="thead list-header" ref="thead">
                        <li class="name" @click="sortStringField('name' , 1)">NAME</li>
                        <li class="lvl" @click="sortNumberField('lvl' , 1)">LVL</li>
                        <li class="itl" @click="sortNumberField('itl' , 1)">ITL</li>
                        <li class="faction" @click="sortStringField('faction' , 1)">FACTION</li>
                        <li class="class" @click="sortStringField('class' , 1)">CLASS</li>
                        <li class="spec" @click="sortStringField('spec' , 1)">SPEC</li>
                        <li class="guild" @click="sortStringField('guild' , 1)">GUILD</li>
                        <li class="login" @click="sortStringField('login' , 1)">LAST LOGIN</li>
                    </ul>
                    <ul v-for="(character, index) in data" v-bind:key="index" :character="character" class="tbody item" ref="tbody">
                        <li class="name" :class="[character?.info?.class ? character?.info?.class.toLowerCase().replace(/ /g, '-').replace(/'/g, '') : '']">{{character.name}}</li>
                        <li class="lvl">{{character?.info?.lvl}}</li>
                        <li class="itl">{{character?.info?.itl}}</li>
                        <li class="faction" :class="[character?.info?.faction ? character?.info?.faction.toLowerCase().replace(/ /g, '-').replace(/'/g, '') : '']">{{character?.info?.faction}}</li>
                        <li class="class">{{character?.info?.class}}</li>
                        <li class="spec">{{character?.info?.spec}}</li>
                        <li class="guild">{{character?.info?.guild}}</li>
                        <li class="login">{{ character?.info?.login ? new Date(character?.info?.login).toISOString().substring(0, 10) : ""}}  </li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default{
    data() {
        return {
            data: [],
            sortField: "name",
            sortDirenction: "sortNormal",
        }
    },
    methods: {
        // sort
        sortNumberField(s , reverse) {
            if (reverse) {
                if(s === this.sortField) {
                    this.sortDirenction = this.sortDirenction==="sortNormal" ? "sortReverse" : "sortNormal";
                } else {
                    this.sortDirenction = this.sortDirenction==="sortReverse" ? "sortReverse" : "sortReverse";
                    this.sortDirenction = this.sortDirenction==="sortNormal" ? "sortReverse" : "sortReverse";
                }   
            }
            this.sortField = s;
            this.sortNumberData()

            Array.from(this.$refs.table.getElementsByClassName("thead")[0].getElementsByTagName("li")).forEach( el => {
                if ( el.classList[0] !== this.sortField ) {
                    el.classList.remove("active")
                } else {
                    el.classList.add("active")
                }
                
                if ( this.sortDirenction == "sortReverse" ) {
                    el.classList.add("sortReverse")
                    el.classList.remove("sortNormal")
                } else {
                    el.classList.add("sortNormal")
                    el.classList.remove("sortReverse")
                } 
            });
        },
        sortStringField(s, reverse) {
            if (reverse) {
                if(s === this.sortField) {
                    this.sortDirenction = this.sortDirenction==="sortNormal" ? "sortReverse" : "sortNormal";
                } else {
                    this.sortDirenction = this.sortDirenction==="sortReverse" ? "sortNormal" : "sortNormal";
                    this.sortDirenction = this.sortDirenction==="sortNormal" ? "sortNormal" : "sortNormal";
                }
            }
            this.sortField = s;
            this.sortStringData()

            Array.from(this.$refs.table.getElementsByClassName("thead")[0].getElementsByTagName("li")).forEach( el => {
                if ( el.classList[0] !== this.sortField ) {
                    el.classList.remove("active")
                } else {
                    el.classList.add("active")
                }
                
                if ( this.sortDirenction == "sortReverse" ) {
                    el.classList.add("sortReverse")
                    el.classList.remove("sortNormal")
                } else {
                    el.classList.add("sortNormal")
                    el.classList.remove("sortReverse")
                } 
            });
        },
        sortNumberData() {
            return this.data.sort((a,b) => {
                let modifier = 1;
                if(this.sortDirenction === "sortReverse") modifier = -1;
                if(Number(a?.info?.[this?.sortField]) < Number(b?.info?.[this?.sortField])) return -1 * modifier;
                if(Number(a?.info?.[this?.sortField]) > Number(b?.info?.[this?.sortField])) return 1 * modifier;
                return 0;
            }).filter((row, index) => {
                let start = (Number(this.dashboadUsersCurrentPage)-1)*Number(this.dashboadUsersPerPage);
                let end = Number(this.dashboadUsersCurrentPage)*Number(this.dashboadUsersPerPage);
                if(index >= start && index < end) return true;
            });
        },
        sortStringData() {
            return this.data.sort((a,b) => {
                let modifier = 1;
                if(this.sortDirenction === "sortReverse") modifier = -1;
                if(a?.info?.[this?.sortField] < b?.info?.[this?.sortField]) return -1 * modifier;
                if(a?.info?.[this?.sortField] > b?.info?.[this?.sortField]) return 1 * modifier;
                return 0;
            }).filter((row, index) => {
                let start = (Number(this.dashboadUsersCurrentPage)-1)*Number(this.dashboadUsersPerPage);
                let end = Number(this.dashboadUsersCurrentPage)*Number(this.dashboadUsersPerPage);
                if(index >= start && index < end) return true;
            });
        },

        refresh() {
            this.data = JSON.parse(JSON.stringify(this.$store.state.charactersData));
        }
    },
    created() {
        this.refresh()
    },
    computed: {
        watch_charactersData() {
            return JSON.parse(JSON.stringify(this.$store.state.charactersData));
        },
    },
    watch: {
        watch_charactersData() {
            this.refresh()
            this.sortStringField(this.sortField)
        },
    },
}
</script>

<style lang="sass" scoped>
@use '@/sassModules/base'
@use '@/sassModules/character-list'
@use '@/sassModules/class'
@use '@/sassModules/variables'

#character-list
    margin: 40px 0
    .inner
        padding: 20px
        background: variables.$dark
        border-radius: variables.$rad-lg
        .title 
            color: variables.$white
            margin-bottom: 20px
</style>
