<template>
  <router-view/>
</template>

<script>
import axios from "axios";

export default {
  methods: {
    async getAccessToken(){
      try {
        let result1 = await axios.post("https://us.battle.net/oauth/token?grant_type=client_credentials&client_id="+this.$store.state.accessData.clientID+"&client_secret="+this.$store.state.accessData.clientSecret, {})
        return this.$store.state.accessData.accessToken = result1.data.access_token
      }
      catch(error) {
        console.log("token" , error)
      }
    },
    async getCharacterDataValidation(){
      await this.$store.state.charactersData.map((item , index) => {
        this.$store.state.charactersData[index] = {
          ...this.$store.state.charactersData[index],
          realm: item.realm.toLowerCase().replace(/ /g, '-').replace(/'/g, ''),
          name: item.name.toLowerCase(),
          nameSpace: "profile-" + item.region,
          nameSpaceStatic: "static-" + item.region,
          nameSpaceDynamic: "dynamic-" + item.region
        }
      })
    },
    getCharacterInfo(){
      this.$store.state.charactersData.map( async ( _ , index) => {
        try {
          let result2 = await axios.get("https://"+this.$store.state.charactersData[index].region+".api.blizzard.com/profile/wow/character/"+this.$store.state.charactersData[index].realm+"/"+this.$store.state.charactersData[index].name+"?namespace="+this.$store.state.charactersData[index].nameSpace+"&locale="+this.$store.state.locale+"&access_token="+this.$store.state.accessData.accessToken)
            console.log(result2.data)


            this.$store.state.charactersData[index].info = {
                name: result2.data?.name,
                lvl: result2.data?.level,
                itl: result2?.data?.equipped_item_level,
                faction: result2?.data?.faction?.name,
                class: result2?.data?.character_class?.name,
                spec: result2?.data?.active_spec?.name,
                guild: result2?.data?.guild ? result2?.data?.guild?.name : "",
                login: result2?.data?.last_login_timestamp
            }
        }
        catch(error) {
          console.log("character" , error , this.$store.state.charactersData[index])
        }
      })
    },
    async accumulateFunc() {
      await this.getAccessToken()
      await this.getCharacterDataValidation()
      await this.getCharacterInfo()
    }
  },
  
  created() {
    this.accumulateFunc()
  }
}
</script>

